import React from 'react';

const ActionProvider = ({ createChatBotMessage, setState, children }) => {
  const handleAbout = () => {
    const botMessage = createChatBotMessage('Je suis Chauffour Evan, Actuellement en BUT Métier du Multimédia et de l\'internet');

    setState((prev) => ({
      ...prev,
      messages: [...prev.messages, botMessage],
    }));
  };

  const handleSkills = () => {
    const botMessage = createChatBotMessage('Je suis juste trop chaud');

    setState((prev) => ({
      ...prev,
      messages: [...prev.messages, botMessage],
    }));
  };

  const handleHobbies = () => {
    const botMessage = createChatBotMessage('Je suis passioné de sport et plus précisément de basket, de cinéma et surtout de développement web.');

    setState((prev) => ({
      ...prev,
      messages: [...prev.messages, botMessage],
    }));
  };

  return (
    <div>
      {React.Children.map(children, (child) => {
        return React.cloneElement(child, {
          actions: {
            handleAbout,
            handleSkills,
            handleHobbies,
          },
        });
      })}
    </div>
  );
};

export default ActionProvider;